<template>
  <div class="recipe-search-container">
    <h1>Recipe Search</h1>
    <div class="search-bar">
      <b-form-input
        v-model="searchQuery"
        placeholder="Search for recipes..."
        @input="searchRecipes"
      ></b-form-input>
      <b-dropdown id="filter-dropdown" text="Filters" class="m-md-2">
        <b-dropdown-item>
          <b-form-select v-model="selectedCuisine" :options="cuisineOptions" @change="searchRecipes"></b-form-select>
        </b-dropdown-item>
        <b-dropdown-item>
          <b-form-select v-model="selectedDiet" :options="dietOptions" @change="searchRecipes"></b-form-select>
        </b-dropdown-item>
        <b-dropdown-item>
          <b-form-select v-model="selectedIntolerance" :options="intoleranceOptions" @change="searchRecipes"></b-form-select>
        </b-dropdown-item>
      </b-dropdown>
      <b-form-select v-model="resultsLimit" :options="resultLimitOptions" @change="searchRecipes"></b-form-select>
    </div>
    <div class="search-results">
      <RecipePreviewList
        v-if="recipes.length > 0"
        :initialRecipes="recipes"
        class="SearchResults"
        :username="$root.store.username"
      />
      <p v-else>No recipes found.</p>
    </div>
  </div>
</template>

<script>
import RecipePreviewList from "../components/RecipePreviewList";
import { BFormInput, BFormSelect, BDropdown, BDropdownItem } from "bootstrap-vue";

export default {
  components: {
    RecipePreviewList,
    BFormInput,
    BFormSelect,
    BDropdown,
    BDropdownItem,
  },
  data() {
    return {
      searchQuery: "",
      recipes: [],
      selectedCuisine: "",
      selectedDiet: "",
      selectedIntolerance: "",
      resultsLimit: 5,
      cuisineOptions: [
        { value: "", text: "No filter" },
        { value: "african", text: "African" },
        { value: "american", text: "American" },
        { value: "chinese", text: "Chinese" },
        // add more cuisines as needed
      ],
      dietOptions: [
        { value: "", text: "No filter" },
        { value: "gluten free", text: "Gluten Free" },
        { value: "ketogenic", text: "Ketogenic" },
        { value: "vegetarian", text: "Vegetarian" },
        // add more diets as needed
      ],
      intoleranceOptions: [
        { value: "", text: "No filter" },
        { value: "dairy", text: "Dairy" },
        { value: "egg", text: "Egg" },
        { value: "gluten", text: "Gluten" },
        // add more intolerances as needed
      ],
      resultLimitOptions: [
        { value: 5, text: "5" },
        { value: 10, text: "10" },
        { value: 15, text: "15" },
      ],
    };
  },
  mounted() {
    const lastSearch = localStorage.getItem("lastSearch");
    if (lastSearch) {
      const { searchQuery, selectedCuisine, selectedDiet, selectedIntolerance, resultsLimit } = JSON.parse(lastSearch);
      this.searchQuery = searchQuery;
      this.selectedCuisine = selectedCuisine;
      this.selectedDiet = selectedDiet;
      this.selectedIntolerance = selectedIntolerance;
      this.resultsLimit = resultsLimit;
      this.searchRecipes();
    }
  },
  methods: {
    async searchRecipes() {
      const query = this.searchQuery;
      const cuisine = this.selectedCuisine;
      const diet = this.selectedDiet;
      const intolerance = this.selectedIntolerance;
      const limit = this.resultsLimit;

      const params = {
        query,
        cuisine,
        diet,
        intolerance,
        number: limit,
      };

      // Save the search state in local storage
      localStorage.setItem(
        "lastSearch",
        JSON.stringify({ searchQuery: query, selectedCuisine: cuisine, selectedDiet: diet, selectedIntolerance: intolerance, resultsLimit: limit })
      );

      try {
        // Replace the following URL with your API endpoint
        const response = await fetch(`https://api.spoonacular.com/recipes/complexSearch?apiKey=YOUR_API_KEY&query=${params.query}&cuisine=${params.cuisine}&diet=${params.diet}&intolerances=${params.intolerance}&number=${params.number}`);
        const data = await response.json();
        this.recipes = data.results;
      } catch (error) {
        console.error("Error fetching recipes:", error);
      }
    },
  },
};
</script>

<style scoped>
.recipe-search-container {
  padding: 20px;
}

.search-bar {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.search-results {
  margin-top: 20px;
}

.SearchResults {
  margin: 10px 0;
}
</style>
